<template>
    <div class="box">
       <Header>
      <div slot="center">订单详情</div>
    </Header>
        <div class="content" style="background-color:#fff;">
          <div class="shop">
          <div class="logistics">
            <span class="iconfont icon-daifahuodingdan logicon"></span>
            <div class="logdiv" >
              <p class="loginfo" v-if="status == 0">[自提柜]已签收，签收人凭取货码签收，感谢使用共享社区丰巢【自提柜】，期待再次为您服务。</p>
              <p class="loginfo" v-else>[自提柜]已签收，签收人凭取货码签收，感谢使用共享社区丰巢【自提柜】，期待再次为您服务。</p>
              <p class="logtime">2019-11-04</p>
            </div>
            <span class="iconfont icon-arrow-sl logicon"></span>
          </div>
          <div class="shou-address">
            <span class="iconfont icon-shouhuodizhi addicon"></span>
            <div class="address-info">
              <p class="name">{{orderlist[0].name}}<span class="add-t">{{orderlist[0].tel}}</span></p>
              <p class="address-d">{{orderlist[0].address}}</p>
            </div>
          </div>
          <div class="line"></div>
            <ul v-for="item of orderlist" :key="item.orderid">
              <li v-for="it of item.list" :key="it.proid">
                <div class="img">
                  <img :src="it.proimg" alt="">
                </div>
                <div class="info">
                  <h1>{{it.proname}}</h1>
                  <div>
                    <span class="price" style="color:#ff5000;font-size:16px;">¥{{it.price.toFixed(2)}}</span>
                    <span class="num">X {{it.num}}</span>
                    <span class="tui" v-if="status != 0">退款</span>
                  </div>
                </div>
              </li>
            </ul>
        </div>
        <div class="jiage">
            <van-cell-group>
                <van-cell title="商品金额" :value="'￥' + orderlist[0].price.toFixed(2)" value-class="red" />
                <van-cell title="运费" value="00.00" />
                <van-cell title="订单编号：" value="68852342730926418" />
                <van-cell title="支付宝交易号：" value="2019111122002199111144517187626" />
                <van-cell title="创建时间：" value="2019-11-1 23：23：51" />
                <van-cell title="付款时间：" value="2019-11-1 23：24：31" />
                <van-cell title="发货时间：" value="2019-11-2 16：01：51" />
            </van-cell-group>
        </div>
        <div class="shou-btn">
          <p v-if="status == 1">延长收货</p>
          <p v-if="status != 0" @click="showwuliu()">查看物流</p>
          <p v-if="status == 0" @click="delOrder(orderlist[0].orderid)">取消订单</p>
          <p v-if="status == 0" @click="gopay(orderlist[0].orderid)">去付款</p>
          <p v-else-if="status == 1"  @click="confirm(orderlist[0].orderid)">确认收货</p>
          <p v-else-if="status == 2"  @click="goEvaluate(orderlist[0].orderid)">去评价</p>
          <p v-else-if="status == 3"  @click="service(orderlist[0].orderid)">售后</p>
        </div>
        </div>
    </div>
</template>

<style lang="scss" scoped>
@import '@/lib/reset.scss';
.header {
  @include rect(100%, 0.44rem);
  @include background-color(#101013);
  @include flexbox();
  @include color(#fff);
  @include justify-content(space-between);
  @include align-items();
  @include padding(10px);
  font-size: 12px;
    div {
    @include flexbox();
    }
    .header-left {
    width: .5rem;
    }
    .header-right {
      span {
        margin: 0 7px;
      }
    }
}
.shop {
  @include rect(100%, auto);
  .logistics {
    @include flexbox();
    @include justify-content(space-between);
    @include align-items();
    padding: .2rem 0 .1rem;
    width:100%;
    border-bottom:1px solid #eee;
    .logicon {
      padding: 0.1rem;
      display: block;
      font-size: 30px;
    }
    .logdiv {
      color: #359a40;
      .logtime {
        color:#aaa;
      }
    }
  }
  .shou-address {
    @include flexbox();
    // @include justify-content(space-between);
    @include align-items();
    .addicon {
      padding: 0.1rem;
      display: block;
      font-size: 30px;
      color:#ff5000;
    }
    .address-info {
      flex:1;
      p {
        font-size:16px;
        font-weight: 500;
        span {
          font-size:14px;
          color:#aaa;
          margin-left:10px;
        }
      }
    }

  }
  .line {
    height:7px;
    background: #eee;
    margin:10px 0;
  }
  ul {
    @include rect(100%, auto);
    padding:10px;
    li {
      @include rect(100%, 1rem);
      @include border(0 0 1px 0, #efefef, solid);
      @include flexbox();
      // @include justify-content(space-between);
      margin: 0 10px;
      .img {
        @include rect(1rem, 1rem);
        img {
          @include rect(0.9rem, 0.9rem);
          @include border(1px, #f66, solid);
          @include margin(0.05rem);
          @include display(block);
        }
      }
      .info {
        margin-left:10px;
        padding-top:10px;
        @include flexbox();
        flex:1;
        justify-content: space-around;
        h1 {
          letter-spacing: 1px;
          width: 170px;
          height: 70px;
          font-size: 10px;
          overflow: hidden;
          font-weight: 100;
        }
        div {
          width: auto;
          // height :130px;
          span:nth-child(3){
            width: 54px;
            height: 22px;
            background-color: rgb(255, 68, 68);
            border-radius: 20px;
            text-align: center;
            line-height: 22px;
            color: white;
            font-weight: bold;
          }
          span {
            display: block;
            margin:2px 0;
            text-align: right;
          }
        }
      }
    }
  }
}
.jiage {
  .van-cell {
    padding:3px 10px;
  }
  .red {
    font-size:20px;
    color:#ff5000;
  }
}
.shou-btn {
  @include flexbox();
  justify-content: flex-end;
  margin-top:20px;
  padding-bottom:20px;
  p {
    width: 25%;
    border: 1px solid #aaa;
    border-radius: 20px;
    margin-right: 10px;
    text-align: center;
    height: .3rem;
    line-height: .28rem;
  }
}
</style>
<script>
import Header from '@/components/Header'
import axios from '@/utils/request'
import Vue from 'vue'
import { Cell, CellGroup, Dialog, Toast } from 'vant'
Vue.use(Cell).use(CellGroup)
Vue.use(Dialog)
Vue.use(Toast)
export default {
  components: {
    Header
  },
  data () {
    return {
      orderlist: [],
      status: ''
    }
  },
  created () {
    let userid = localStorage.getItem('userid')
    let orderid = this.$route.query.orderid
    this.status = this.$route.query.status
    axios.get('/order?userid=' + userid + '&orderid=' + orderid).then(res => {
      console.log(res.data.data)
      this.orderlist = res.data.data
    })
  },
  methods: {
    confirm (orderid) {
      Dialog.confirm({
        title: '确认收货',
        message: '是否确认收货'
      }).then(() => {
        axios.get('/order/updatestatus?orderid=' + orderid + '&status=' + 2).then(res => {
          Toast('已收货')
          this.$router.replace('userorder?num=' + 2)
        })
        console.log(orderid)
      }).catch(() => {
        // on cancel
      })
    },
    gopay (orderid) {
      this.$router.push('/order?orderid=' + orderid)
    },
    delOrder (orderid) {
      Dialog.confirm({
        title: '删除订单',
        message: '是否删除该订单'
      }).then(() => {
        axios.get('order/delete?orderid=' + orderid).then(res => {
          Toast('删除成功')
          this.$router.replace('userorder?num=' + 1)
        })
        console.log(orderid)
      }).catch(() => {
        // on cancel
      })
    },
    service (orderid) {
      Dialog.alert({
        message: '亲，我是假的哦'
      })
    },
    goEvaluate (orderid) {
      this.$router.push('/evaluate?orderid=' + orderid)
    },
    showwuliu () {
      Dialog.alert({
        message: '亲，请收货拉'
      })
    }
  }
}
</script>
